<template>
  <div>
    <!-- 面包屑导航区域 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>数据统计</el-breadcrumb-item>
      <el-breadcrumb-item>转化率分析</el-breadcrumb-item>
    </el-breadcrumb>

    <!-- 卡片视图区域 -->
    <el-card>
      <!-- 2. 为ECharts准备一个具备大小（宽高）的Dom -->
      <div id="main" style="width: 80%;height:400px;"></div>
      <Plugin></Plugin>
    </el-card>
  </div>
</template>

<script>
// 1. 导入 echarts
import echarts from 'echarts'
import Plugin from './plugin'

export default {
  components: {
    Plugin
  },
  data () {
    return {
      // 画图需要的数据
      option: {
        title: {
          text: '流量分布',
          subtext: '纯属虚构',
          left: 'left',
          top: 'bottom'
        },
        tooltip: {
          trigger: 'item',
          formatter: '{a} <br/>{b} : {c}%'
        },
        toolbox: {
          orient: 'vertical',
          top: 'center',
          feature: {
            dataView: { readOnly: false },
            restore: {},
            saveAsImage: {}
          }
        },
        legend: {
          orient: 'vertical',
          left: 'left',
          data: ['展现', '点击', '访问', '咨询', '订单']
        },

        series: [
          {
            name: '分布下降',
            type: 'funnel',
            width: '40%',
            height: '45%',
            left: '5%',
            top: '50%',
            data: [
              { value: 60, name: '访问' },
              { value: 30, name: '咨询' },
              { value: 10, name: '订单' },
              { value: 80, name: '点击' },
              { value: 100, name: '展现' }
            ]
          },
          {
            name: '数据上升',
            type: 'funnel',
            width: '40%',
            height: '45%',
            left: '5%',
            top: '5%',
            sort: 'ascending',
            data: [
              { value: 60, name: '访问' },
              { value: 30, name: '咨询' },
              { value: 10, name: '订单' },
              { value: 80, name: '点击' },
              { value: 100, name: '展现' }
            ]
          },
          {
            name: '转化率下降',
            type: 'funnel',
            width: '40%',
            height: '45%',
            left: '55%',
            top: '5%',
            label: {
              position: 'left'
            },
            data: [
              { value: 60, name: '访问' },
              { value: 30, name: '咨询' },
              { value: 10, name: '订单' },
              { value: 80, name: '点击' },
              { value: 100, name: '展现' }
            ]
          },
          {
            name: '今日数据',
            type: 'funnel',
            width: '40%',
            height: '45%',
            left: '55%',
            top: '50%',
            sort: 'ascending',
            label: {
              position: 'left'
            },
            data: [
              { value: 60, name: '访问' },
              { value: 30, name: '咨询' },
              { value: 10, name: '订单' },
              { value: 80, name: '点击' },
              { value: 100, name: '展现' }
            ]
          }
        ]
      }
    }
  },
  created () {},
  // 此时，页面上的元素，已经被渲染完毕了！
  async mounted () {
    // 3. 基于准备好的dom，初始化echarts实例
    var myChart = echarts.init(document.getElementById('main'))

    myChart.setOption(this.option)
  },
  methods: {
  }
}
</script>

<style lang="less" scoped>
</style>
